import React from 'react';

class Header extends React.Component {
  render() {
    return (
      <div className='header col-xs-12 col-md-12' style={{background: 'url("../styles/assets/images/mobile/bg_header.jpg")',borderBottom: '1px solid #fff'}}>
        <div className='col-xs-10'></div>
        <div className='col-xs-2' style={{paddingRight: 5}}><i className='fa fa-bars fa-2x pull-right'></i></div>
      </div>
    )
  }
}

export default Header;




