import React from 'react';

class MainImage extends React.Component {
  render() {
    return (
      <div className='col-xs-12 col-md-12' style={{background: 'url("../styles/assets/images/mobile/prod.jpg")', backgroundSize: 'contain'}}>
        <div className='col-xs-10'></div>
      </div>
    )
  }
}

export default MainImage;




